/* Font */

@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,900|Titillium+Web:300,400,600,700,900");

/*--------------------------------------------------------------
  ##  Normalize
  --------------------------------------------------------------*/

html {
  direction: rtl;
}

h1 {
  margin: 0;
}

a {
  background-color: transparent;
  text-decoration: none;
}

a,
a:hover {
  text-decoration: none;
  color: inherit;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

img {
  border-style: none;
  max-width: 100%;
}

button,
input,
optgroup,
select,
textarea {
  font-size: 100%;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: button;
}

button:hover {
  border: 0 solid;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0;
}

legend {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="checkbox"],
[type="radio"] {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
}
:focus-visible {
  outline: none;
}

input[type="number"] {
  appearance: textfield;
}

[type="search"] {
  appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

[hidden] {
  display: none;
}

a:focus,
input:focus,
button:focus,
-moz-button:focus {
  outline: none;
}

/*--------------------------------------------------------------
  ##  Typography
  --------------------------------------------------------------*/

body {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  line-height: 25px;
  color: #4c455a;
  -moz-osx-font-smoothing: grayscale;
}

span,
a,
button,
input {
  font-family: "Open Sans", sans-serif;
}

button:focus,
input:focus,
select:focus,
optgroup:focus,
textarea:focus {
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  clear: both;
  font-weight: 700;
  line-height: 1.1;
  font-family: "Titillium Web", sans-serif;
}

h2 {
  font-size: 60px;
}

h3 {
  font-size: 40px;
}

h4 {
  font-size: 30px;
}

h5 {
  font-size: 20px;
}

h6 {
  font-size: 16px;
}

dfn,
cite,
em,
i {
  font-style: italic;
}

blockquote {
  margin: 0;
}

address {
  margin: 0;
}

code,
kbd,
tt,
var {
  font-size: 16px;
}

abbr,
acronym {
  border-bottom: 1px dotted;
  cursor: help;
}

mark,
ins {
  text-decoration: none;
}

/*--------------------------------------------------------------
  ##  Elements
  --------------------------------------------------------------*/

html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-size: 100%;
  -webkit-text-size-adjust: 100%;
  font-variant-ligatures: none;
  -webkit-font-variant-ligatures: none;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  text-shadow: rgba(0, 0, 0, 0.01) 0 0 1px;
}

*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

hr {
  border: 0;
  height: 1px;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

li > ul,
li > ol {
  list-style: none;
}

img {
  height: auto;
  max-width: 100%;
}

figure {
  margin: 0;
}

table {
  margin: 0;
  width: 100%;
}

a {
  padding: 0;
  margin: 0;
}

.custom-container {
  width: 95%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 1200px) {
  .container {
    max-width: 1230px;
  }
}

@media (min-width: 1200px) and (max-width: 1300px) {
  .container {
    max-width: 1140px;
  }
}

/*--------------------------------------------------------------
  ##  Forms
  --------------------------------------------------------------*/
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
