  margin-left: 20px;
  width: 160px;
}

.fixed .logo {
  margin-top: 10px;
  float: right;
  width: 115px;
  margin-right: 55px;
  margin-bottom: 9px;
}

.mainmenu > ul {
  margin: 0;
  padding-top: 5px;
}

.mainmenu > ul li {
  display: inline-block;
  position: relative;
}

.mainmenu > ul li a {
  font-weight: 700;
  color: #000;
  padding: 6px 12px;
  display: inline-block;
  font-size: 13px;
  text-transform: uppercase;
  font-family: "Open Sans", sans-serif;
  letter-spacing: 2.6px;
  position: relative;
  text-decoration: none;
}

.mainmenu > ul > li > a::after {
  content: "";
  position: absolute;
  left: 12px;
  top: 100%;
  width: 0;
  height: 1px;
  background: #6a6969;
  transition: all 0.2s;
}

.mainmenu > ul li a:hover:after,
.mainmenu > ul li a.active:after {
  width: 47%;
  transition: all 0.5s;
}

.mainmenu li.has-child > a {
  position: relative;
}

/*--------------------------------------------------------------
    Dropdown menu
    --------------------------------------------------------------*/
.sub-menu {
  position: absolute;
  z-index: 99999;
  background-color: #fff;
  width: 255px;
  box-shadow: 0px 10px 16px 0px rgba(36, 31, 31, 0.31);
  -webkit-box-shadow: 0px 10px 16px 0px rgba(36, 31, 31, 0.31);
  top: 115%;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  padding: 20px 0;
  right: 0;
  border: 1px solid #eeeeee;
  border-radius: 2px;
}

.sub-menu li {
  position: relative;
  text-align: left;
}

.mainmenu ul li .sub-menu li a {
  padding: 8px 35px;
  width: 100%;
  display: block;
  color: #9b9b9b;
  font-size: 13px;
  text-transform: capitalize;
  font-weight: 700;
  letter-spacing: 0;
  padding-right: 0;
  position: relative;
  transition: all 0.3s;
}

.mainmenu ul li .sub-menu li a:hover {
  color: #000;
  transition: all 0.3s;
}

.mainmenu ul li .sub-menu li a:hover:before {
  background: #000;
  transition: all 0.3s;
}

.mainmenu ul li .sub-menu li a::before {
  position: absolute;
  content: "";
  left: 26px;
  top: 50%;
  width: 4px;
  height: 4px;
  background: #9b9b9b;
  border-radius: 100%;
  margin-top: -2px;
  transition: all 0.3s;
}

.mainmenu ul li .sub-menu li.has-child > a::after {
  position: absolute;
  content: "";
  right: 26px;
  top: 50%;
  width: 20px;
  height: 1px;
  background: #000;
  border-radius: 100%;
  margin-top: 1px;
  transition: all 0.6s;
}

.mainmenu li:hover .sub-menu {
  visibility: visible;
  top: 103%;
  opacity: 1;
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}

.mainmenu li:hover .sub-menu .sub-menu {
  position: absolute;
  top: 0;
  right: 100%;
  opacity: 0;
  visibility: hidden;
}

.mainmenu li:hover .sub-menu li:hover .sub-menu {
  opacity: 1;
  visibility: visible;
}

.mainmenu ul li .sub-menu li {
  width: 100%;
  position: relative;
}

.mainmenu ul li .sub-menu li:after {
  content: "";
  position: absolute;
  left: 2px;
  width: 6px;
  height: 28px;
  background: #efefef;
  top: 7px;
  transition: all 0.3s;
  opacity: 0;
}

.mainmenu ul li .sub-menu li:hover:after {
  height: 28px;
  transition: all 0.5s;
  opacity: 1;
}

