/*----------------------------------------*/
/*  ##   Offcanvus
/*----------------------------------------*/

.offcanvus-menu {
  width: 400px;
  background: #000;
  height: 100%;
  position: fixed;
  top: 0;
  z-index: 999999;
  padding-left: 50px;
  padding-top: 130px;
  right: -405px;
  transition: all 0.5s;
}

.close-offcanvus .svg {
  width: 14px;
  float: right;
  /* position: absolute; */
  height: 17px;
  margin-left: 5px;
  margin-right: 6px;
  overflow: hidden;
  margin-top: 5px;
}

.open.offcanvus-menu {
  right: 0;
  transition: all 0.5s;
  transition-delay: 0.3s;
}

.offcanvus-menu ul {
  transition: all 1s;
}

.menu-open .offcanvus-menu ul {
  padding-left: 0;
  transition: all 0.5s;
  transition-delay: 0.3s;
}

.close-offcanvus {
  color: #fff;
  position: absolute;
  top: 0;
  right: 0;
  background: #000;
  padding: 5px 10px;
  cursor: pointer;
}

.close-offcanvus i {
  float: right;
  /* display: inline-block; */
  margin-right: 10px;
  margin-top: 6px;
  font-size: 14px;
}

.close-text {
  float: right;
  text-transform: uppercase;
  font-size: 13px;
}

.offcanvus-menu ul li {
  margin-bottom: 20px;
}

.offcanvus-menu > ul li a {
  font-size: 20px;
  color: #c2bdbd;
  text-transform: capitalize;
  position: relative;
  text-decoration: none;
}

.offcanvus-menu > ul li a:hover {
  color: #fff;
  transition: all 0.3s;
}

.offcanvus-menu ul li a::after {
  content: "";
  position: absolute;
  left: -50px;
  top: 15px;
  width: 0;
  height: 1px;
  background: #fff;
  transition: all 0.3s ease-in-out;
}

.offcanvus-menu ul li a:hover:after {
  width: 35px;
  transition: all 0.3s ease-in-out;
}

.offcanvus-menu ul li a.active:after {
  width: 35px;
  transition: all 0.3s ease-in-out;
}

.offcanvus-menu .footer-social-light ul li a {
  color: #c2bdbd;
  display: block;
  font-size: 12px;
}

.offcanvus-menu .footer-social-light ul li {
  width: 32px;
  height: 32px;
  line-height: 31px;
}

.offcanvus-menu .footer-social-light {
  text-align: center;
  position: absolute;
  bottom: 60px;
}

.mask-overlay {
  position: fixed;
  top: 0;
  right: -100%;
  width: 100%;
  background: #fff;
  height: 100%;
  z-index: 9999;
  opacity: 0;
  transition: all 1s;
}

.mask-overlay::before {
  content: "";
  position: absolute;
  right: -100%;
  top: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0;
  transition: all 1.5s;
}

.open.mask-overlay {
  transition-delay: 0.3s;
  opacity: 0.5;
  right: 0;
  transition: all 0.5s;
}

.open.mask-overlay:before {
  opacity: 0.6;
  right: 0;
  transition: all 1s;
}

