  }
}

.video-inner a::before {
  content: "";
  position: absolute;
  width: 195%;
  height: 205%;
  background: #000;
  transform: rotate(45deg);
  left: -126%;
  top: 106%;
  z-index: -1;
}

.video-inner a:hover::before {
  animation: bbb 0.7s 1;
  top: -20%;
  left: -29%;
}

.video-inner a:hover {
  border: 4px solid rgba(0, 0, 0, 1);
